.lateral-nav {
  display: none;

  @include smmin {
    display: block;
    position: fixed;
    right: 10px;
    top: 40%;
    z-index: 10;

    a {
      display: block;
      position: relative;
      margin-bottom: 15px;
    }

    i {
      display: block;
      background: white;
      border: 1px solid #444;
      border-radius: 50%;
      width: 15px;
      height: 15px;
    }

    span {
      opacity: 0;
      position: absolute;
      top: 0;
      text-align: right;
      white-space: pre;
      line-height: 15px;
      transition: transform 0.3s ease, opacity 0.3s ease;
    }

    a:hover,
    a.is-active {
      i {
        background: #444;
      }
      span {
        opacity: 1;
        transform: translateX(-110%);
      }
    }
  }
}
