.header {
  position: fixed;
  z-index: 50;
  background: white;
  box-shadow: -0.125rem 0.1875rem 1.9375rem -0.375rem rgba(0, 0, 0, 0.75);
  top: 0;
  left: 0;
  right: 0;
  height: 6.875rem;
  transition: height 0.3s ease; }
  @media screen and (max-width: 767px) {
