    width: 100%;
    display: flex;
    align-items: center;
    transition: padding 0.3s ease; }
  .header__logo {
    display: flex;
    align-items: center;
    height: 5.625rem;
    width: 5.625rem;
    text-decoration: none;
    transition: height 0.3s ease, width 0.3s ease; }
    @media screen and (max-width: 767px) {
