    .is-small-menu .header .header__logo {
      height: 1.875rem; }
    .is-small-menu .header .header__nav a {
      font-size: 0.75rem; }

body {
  padding-top: 2.5rem; }

.footer {
  background: #02a6e8;
  position: relative;
  color: white;
  padding: 1.875rem 0;
  line-height: 1.6; }
  .footer__container {
    position: relative;
    z-index: 1;
    max-width: 68.75rem;
    padding: 0.625rem;
    margin: 0 auto;
    width: 100%;
    display: grid; }
    @media screen and (min-width: 768px) {
      .footer__container {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
        grid-column-gap: 3.125rem; } }
    @media screen and (min-width: 991px) {
      .footer__container {
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: auto;
        grid-column-gap: 3.125rem; } }
    .footer__container > div {
      position: relative;
      z-index: 1; }
  .footer strong {
    text-transform: uppercase; }
  .footer ul {
    list-style: none;
    padding: 0; }
  .footer img {
    position: absolute;
    right: 0.9375rem;
    bottom: 0.3125rem;
    width: 18.75rem;
    z-index: 0;
    animation: surfer 3s infinite ease; }
  .footer a {
    text-decoration: none; }
    .footer a:hover {
      text-decoration: underline; }

@keyframes toRight {
  0% {
    opacity: 0;
    transform: translateX(-40%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes toLeft {
  0% {
    opacity: 0;
    transform: translateX(40%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes toUp {
  0% {
    opacity: 0;
    transform: translateY(2.5rem); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes toScale {
  0% {
    opacity: 0;
    transform: scale(0.6); }
  100% {
    opacity: 1;
    transform: scale(1); } }

.content {
